.checkbox-wrapper {
  margin: 0 16px;
}
@media (max-width: 1399.98px) {
  .checkbox-wrapper {
    margin: 0 10px;
  }
}
@media (max-width: 575.98px) {
  .checkbox-wrapper {
    margin: 0 5px;
  }
}

.checkbox-wrapper input {
  padding: 0;
  height: initial;
  width: initial;
  margin-bottom: 0;
  display: none;
  cursor: pointer;
}

.checkbox-wrapper label {
  display: flex;
  align-items: center;
  position: relative;
  margin: 0;
  cursor: pointer;
  font-size: 12px;
}
@media (max-width: 991.98px) {
  .checkbox-wrapper label {
    font-size: 16px;
  }
}
@media (max-width: 575.98px) {
  .checkbox-wrapper label {
    font-size: 14px;
  }
}
@media (max-width: 390px) {
  .checkbox-wrapper label {
    flex-direction: column-reverse;
  }
}

.checkbox-wrapper label:before {
  content: "";
  background-color: transparent;
  border: 2px solid;
  border-color: #eff2f5;
  padding: 7px;
  position: relative;
  cursor: pointer;
  margin-right: 5px;
  border-radius: 4px;
  transition: all 0.2s ease-out;
}
@media (max-width: 1199.98px) {
  .checkbox-wrapper label:before {
    padding: 5px;
  }
}
@media (max-width: 991.98px) {
  .checkbox-wrapper label:before {
    padding: 7px;
  }
}
@media (max-width: 575.98px) {
  .checkbox-wrapper label:before {
    padding: 5px;
  }
}

.checkbox-wrapper input:checked + label::before {
  background-color: #eff2f5;
}